@keyframes fade-in-slide {
    0% {
      transform: translateY(-40px);
      opacity: 0;
    }
    50% {
      transform: translateY(-10px);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  
  .card-item {
    border: 1px solid rgba(255, 255, 255, 0.5);
    color: #fafafa;
    padding: 10px 20px;
    display: flex;
    flex-direction: column;
    width: auto;
    overflow: hidden;
    background-color: rgba(12, 12, 12, 0.288);
    animation: 0.8s ease 0s 1 normal forwards running fade-in-slide;
}


.card-item a, h4, p {
    color: #ffffff;
}

.card-item h4 {
    text-transform: capitalize;
}

.card-item p {
    color: #fafafa;
    font-size: small;
    margin-top: 10px;
}

.aditional-info {
    display: inline-block;

}

.fork-info {
    vertical-align: bottom;
    padding: 0 7px;
    font-size: 12px;
    font-weight: 500;
    border-radius: 2em;
    padding-left: 10px;
    color: #fafafa;
    background-color: rgba(10, 214, 95, 0.5);
    border: 1px solid rgb(6, 184, 80);
    margin-left: 5px;
}

.lang-info {
    vertical-align: bottom;
    padding: 0 7px;
    font-size: 12px;
    font-weight: 500;
    border-radius: 2em;
    color: #fafafa;
    background-color: rgba(177, 177, 177, 0.5);
    border: 1px solid rgb(133, 133, 133);
}

.lang-info:empty {
    display: none;
}

.fork-info:empty {
    display: none;
}

